@import (less) "color.less";

@paper-sign-height: 240px;

.paper-sign-container {
  background-color: @lightest_gray;
  height: @paper-sign-height;
}

.paper-sign-container, .paper-image-container, .paper-text-container{
  height: 240px;
}

.paper-image{
  font-size: 140px;
  color: @dark_gray;
  display: inline-block;
  // height: 140px;
  background-repeat: no-repeat;
}

.vertical-alignment {
  transform:translateY(-50%);
  top:50%;
  position:absolute;
  &.orizontal-container {
    transform: translate(-50%,-50%);
    left:50%;
    position: absolute;
  }
}

h1 {
  font-size: 25px;
  margin:0;
  padding-bottom:15px;
  color:@black;
}

p {
  font-size: 16px;
  // font-weight: 700px;
  color:@gray;
  margin-bottom: 30px;
}
.button-container{
  width:25%;
}